@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #08aeea;
  width: 100vw;
  height: 100vh;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#08aeea), to(#2af598));
  background-image: linear-gradient(0deg, #08aeea 0%, #2af598 100%);
}

code {
  font-family: "Roboto", sans-serif;
}

.App-header {
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  color: #fff;
}
.App-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.node {
  border: 1px solid rgba(0, 0, 0, 0.5);
  background: rgba(255, 255, 255, 0.5);
}

.node__start {
  background: green;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}

.node__finish {
  background: red;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}

.node__visited {
  -webkit-animation-name: visitedAnimation;
          animation-name: visitedAnimation;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 100%;
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 190, 218, 0.75);
  }
}

@keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 100%;
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 190, 218, 0.75);
  }
}

.node__wall {
  background-color: rgba(0, 29, 53, 0.932);
  /* animation-name: wallAnimation;
  animation-duration: 0.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards; */
}

@-webkit-keyframes wallAnimation {
  0% {
    -webkit-transform: scale(0.2);
            transform: scale(0.2);
    background-color: rgba(0, 94, 172, 0.932);
    border-radius: 100%;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 29, 53, 0.932);
  }
}

@keyframes wallAnimation {
  0% {
    -webkit-transform: scale(0.2);
            transform: scale(0.2);
    background-color: rgba(0, 94, 172, 0.932);
    border-radius: 100%;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 29, 53, 0.932);
  }
}

.node__shortest-path {
  -webkit-animation-name: shortestPathAnimation;
          animation-name: shortestPathAnimation;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes shortestPathAnimation {
  0% {
    -webkit-transform: scale(0.2);
            transform: scale(0.2);
    background-color: rgba(255, 238, 0, 0.932);
    border-radius: 100%;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(236, 145, 7, 0.932);
  }
}

@keyframes shortestPathAnimation {
  0% {
    -webkit-transform: scale(0.2);
            transform: scale(0.2);
    background-color: rgba(255, 238, 0, 0.932);
    border-radius: 100%;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(236, 145, 7, 0.932);
  }
}

.table {
  margin: 0 auto;
  display: grid;
  grid-gap: 1px;
}

.config-desc {
  display: flex;
  width: 75%;
  margin: 0 auto;
  color: #fff;
}

.config-desc__config {
  flex: 20% 0;
}

.config-desc__pathfinding {
  flex: 30% 0;
}

.config-desc__mazes {
  flex: 50% 0;
}

.config-buttons {
  display: flex;
  justify-content: space-between;
  margin: 25px auto 50px auto;
  width: 75%;
}

.config-buttons button {
  background: none;
  border: 2px solid #fff;
  font-weight: 600;
  padding: 10px 15px;
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  color: #fff;
  font-size: 18px;
  -webkit-transition: all 0.25s ease-in;
  transition: all 0.25s ease-in;
}

.config-buttons button:hover {
  cursor: pointer;
  border: 2px solid #000;
  color: #000;
}

.maze-generators {
  display: flex;
  color: #fff;
}

.maze-generators button {
  display: flex;
  color: #fff;
  margin-left: 20px;
}

